* {
  box-sizing: border-box;
  align-items: center;
}

body {
  background-color: rgb(250, 215, 139);
}
.title {
  text-align: center;
  font-family: "Yellowtail", cursive;
  text-decoration: underline;
}
.main {
  background-color: rgb(247, 211, 54);
  justify-content: center;
  align-items: center;
 height: 650px;
  margin-top: 20px;
  border-radius: 80px;
  border: 5px solid rgb(252, 172, 0);
  box-shadow: 0px 0px 150px;
}
.inputsAndList{
  width:100%;
}

.inputs-btns {
  display: flex;
  justify-content: space-evenly;

}





.btn{
    font-size: larger;
    color: gray;
    cursor: pointer;
    width: 60px;
    margin-right: 50px;
}

li {
  width: 95%;
  list-style-type: none;
  display: flex;
  background: rgb(207, 170, 3);

  &:hover {
    background: rgb(226, 193, 45);
    transition: ease 0.5s;
  }
}

.inputList {
  width: 50%;
  text-align: left;
  transform: translateX(50px);
  margin: 6px 0px 6px 0px;
    font-size: large;
    font-style:normal;
  cursor: pointer;
}

.deleteAndCheckedBtns {
  display: flex;
  width: 50%;
  text-align: right;
  transform: translateX(-50px);
  button {
    cursor: pointer;
  }

}

.faRotateIcon {
  font-size: xx-large;
}

.iconCart {
  font-size: 20px;
  color: rgb(113, 123, 184);
  border: none;
}
.iconBack{
    font-size: 20px;
    color: rgb(177, 115, 0);
    border: none;
    }
.iconRed {
  font-size: 20px;
  color: rgb(155, 0, 0);
  border: none;
}
 @media screen and (max-width: 470px) {
  .inputs-btns{
    flex-direction: column;
    width:100%;
    margin-right: 0px;
    
  
  }
 }
   